
@media only screen and (max-width: 745px) {
  .Home {
    height: auto !important;
    grid-template-columns: 1fr !important;
  }
  .right {
    display: grid !important;
    grid-template-columns: 1fr;
    grid-gap: 0;
  }
  .right .experience {
    width: 100% !important;
    padding-left: 15px;
  }
  .left {
    padding-left: 15px;
  }
  .social_icons_div {
    width: 100% !important;
  }
  .right {
    padding-top: 5% !important;
  }
}

.Home {
